          padding: 5px 10px 8px 0px;
          border-radius: 5px;
          border: none;
          .com-con {
            .img {
              width: 40px;
              height: 40px;
              display: inline-block;
              vertical-align: top;
              border-radius: 50%;
              overflow: hidden;
              img {
                width: 100%;
                height: 100%;
              }
            }
            .com-wb {
              width: 600px;
              margin-left: 15px;
              display: inline-block;
              span {
                font-size: 16px;
                color: #5b5b5b;
                margin: 0 8px;
              }
              h5 {
                font-weight: 400;
                color: #222;
                font-size: 16px;
                display: inline-block;
              }
              .time {
                display: inline-block;
                color: #a8a8a8;
                margin-left: 5px;
              }
              .com-text {
                position: relative;
                p {
                  margin: 15px auto 5px auto;
                  font-size: 16px;
                  color: #5b5b5b;
                  display: inline-block;
                }
                .reply {
                  position: absolute;
                  right: 5px;
                  top: 20px;
                  font-size: 16px;
                  color: #999;
                }
              }
              textarea {
                width: 585px;
                height: 80px;
                border-radius: 5px;
                background-color: #f9f9f9;
                border: none;
              }
            }
          }
        }
      }
      .btn {
        margin-top: 15px;
        a {
          width: 100px;
          height: 30px;
          font-size: 16px;
          color: #8e8e8e;
          float: right;
          line-height: 30px;
          border-radius: 5px;
          text-align: center;
        }
        .com {
          background-color: #ea6557;
          color: #fff;
        }
      }
    }
  }
}

.more {
  text-align: center;
  margin: 50px auto;
  a {
    width: 346px;
    height: 40px;
    font-size: 16px;
    color: #bbb;
    line-height: 40px;
    text-align: center;
    display: inline-block;
    border: 1px solid #cfcfcf;
    border-radius: 5px;
  }
}

.details-content-video-recommend {
  display: inline-block;
  width: 301px;
  margin-left: 35px;
  font-size: 20px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  line-height: 26px;
  color: rgba(234, 100, 87, 1);
  opacity: 1;
  vertical-align: top;
}
.recommend-title {
  font-size: 20px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  line-height: 26px;
  color: rgba(234, 100, 87, 1);
  opacity: 1;
  height: 36px;
  border-bottom: 1px solid rgba(230, 230, 230, 1);
  position: relative;
  margin-bottom: 26px;
}
.recommend-title::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 91px;
  height: 2px;
  background: #ea6457;
}
.recommend-img {
  cursor: pointer;
  .img {
    width: 301px;
    height: 152px;
    margin-bottom: 20px;
    border-radius: 5px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      z-index: -1;
      position: absolute;
    }
  }
  .test {
    font-size: 16px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    line-height: 26px;
    color: rgba(255, 255, 255, 1);
    opacity: 1;
    position: absolute;
    left: 10px;
    bottom: 17px;
    overflow: hidden;
    text-overflow: ellipsis;
    -o-test-overflow:ellipsis;
    white-space: nowrap;
    width:273px;
    display: none;
  }
}
.video {
  position: relative;
  z-index: 1;
  .bf {
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  &:hover .bf {
    display: block;
  }
  &:hover .img {
    background: rgba(0, 0, 0, 0.5);
  }
  &:hover .test{
    display: block; 
  }
}

.recommend-open {
  width: 301px;
  height: 40px;
  background: rgba(246, 246, 246, 1);
  border: 1px solid rgba(216, 216, 216, 1);
  opacity: 1;
  border-radius: 5px;
  text-align: center;
  font-size: 16px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  line-height: 40px;
  color: rgba(142, 142, 142, 1);
  opacity: 1;
  margin-bottom: 59px;
  cursor: pointer;
}
</style>





